.line1 {
    width: 270px;
    height: 10px;
    display: inline-block;
    background: var(--tc-theme-primary);
}
.line2 {
    width: 198px;
    height: 10px;
    display: inline-block;
    background: var(--tc-theme-primary);
}
.digital-agency-home-section-3 {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    &::after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        background-color: var(--tc-system-navy-blue-3);
        opacity: 0.9;
    }
}
.digital-agency-home-section-4 {
    .card-portfolio-bg {
        transition: all 0.3s ease-in-out;
        .card-portfolio {
            transition: all 0.3s ease-in-out;
        }
        &:hover {
            background-image: url(../imgs/pages/digital-agency/page-home/home-section-4/img-bg-card.png);
            .portfolio-card-icon {
                background: var(--tc-theme-primary);
                transition: all 0.3s ease-in-out;
                border-color: var(--tc-theme-primary) !important;
            }
        }
    }
}
.digital-agency-home-section-5 {
    // Swiper customize
    .swiper-button-next,
    .swiper-button-prev {
        --swiper-navigation-size: 16px;
        --swiper-navigation-top-offset: 50%;
        --swiper-navigation-sides-offset: 10px;
        --swiper-navigation-color: var(--tc-system-black);
        display: flex;
        width: 50px;
        height: 50px;
        padding: 0px;
        font-size: 16px;
        border-radius: 50px;
        background-color: var(--tc-system-white);
        border: 1px solid var(--tc-system-navy-blue-3);
        svg {
            width: 16px;
            height: 16px;
        }
        &:hover,
        :active {
            background-color: var(--tc-theme-primary);
            svg {
                path {
                    fill: var(--tc-system-navy-blue-3);
                }
            }
        }
    }
}
.digital-agency-home-section-6 {
    .card {
        transition: all 0.3s ease-in-out;
        .card-header {
            a {
                .arrow {
                    &::before {
                        transition: 0.3s;
                        transform: rotate(0deg);
                        font-family: bootstrap-icons;
                        content: '\F2EA';
                        color: var(--tc-system-navy-blue-3);
                        font-size: 20px;
                    }
                }
                border-bottom: 1px solid transparent;
                &.collapsed {
                    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
                    .arrow {
                        background-color: transparent;
                        &::before {
                            transition: 0.3s;
                            color: var(--tc-system-navy-blue-3);
                            transform: rotate(180deg);
                            content: '\F4FE';
                        }
                    }
                }
            }
        }
    }
}
.section-footer-23 {
    .icon {
        svg {
            height: 14px;
            width: 14px;
            fill: var(--tc-system-white);
            opacity: 0.5;
        }
        &:hover {
            svg {
                fill: var(--tc-system-white);
                opacity: 1;
                transition-duration: 0.3s;
            }
        }
    }
}
.digital-agency-about-section-2 {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    &::after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        background-color: var(--tc-system-navy-blue-3);
        opacity: 0.9;
    }
    .item {
        cursor: pointer;
        border-radius: 16px;
        background: #fff;
        width: 100%;
        padding: 40px;
        transition: all 0.3s ease-in-out;
        &:hover {
            background: var(--tc-theme-primary);
        }
        h1 {
            font-size: 44px;
            font-weight: 600;
        }
    }
}
.card-team {
    .card-team-img {
        border-radius: 16px;
        background: #f3f3f3;
        min-height: 320px;
        max-width: 320px;
    }
    .social-group {
        transition: all 0.2s linear;
        @media (min-width: 1400px) {
            opacity: 0;
        }
        .icon {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            width: 40px;
            height: 40px;
            background: var(--tc-system-white);
            border-radius: 50%;
            transition: all 0.2s linear;
            svg {
                height: 20px;
                width: 20px;
                path {
                    fill: var(--tc-system-black);
                }
            }
            &:hover {
                background: var(--tc-theme-primary);
                svg {
                    path {
                        fill: var(--tc-system-black);
                        transition-duration: 0.2s;
                    }
                }
            }
        }
    }
    &:hover {
        .social-group {
            opacity: 1;
        }
    }
}
.step-item {
    transition: all 0.2s linear;
    &:hover {
        background-color: var(--tc-theme-primary) !important;
        .icon-shape {
            transition: all 0.3s linear;
            background: var(--tc-system-black) !important;
            svg {
                path {
                    transition: all 0.3s linear;
                    stroke: var(--tc-theme-primary) !important;
                }
            }
        }
    }
}
.pagination_item {
    background: var(--tc-bg-light);
    color: var(--tc-system-black);
    border-radius: 8px !important;
    font-size: 20px !important;
    font-weight: 600 !important;
    svg {
        path {
            stroke: var(--tc-system-black) !important;
            transition: 0.3s ease-in-out;
        }
    }
    &.active {
        background: var(--tc-theme-primary);
        color: var(--tc-system-black) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-black) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:hover {
        background: var(--tc-theme-primary);
        color: var(--tc-system-black) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-black) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:focus {
        box-shadow: none;
        background-color: var(--tc-theme-primary) !important;
        color: var(--tc-system-black) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-black) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
}
.digital-agency-services-details-section-1 {
    // nav-tabs
    .nav-tabs {
        --bs-nav-tabs-border-width: var(--bs-border-width);
        --bs-nav-tabs-border-color: var(--bs-border-color);
        --bs-nav-tabs-border-radius: 0;
        --bs-nav-tabs-link-active-color: var(--tc-system-navy-blue-3);
        --bs-nav-tabs-link-hover-border-color: transparent;
    }
    .nav {
        --bs-nav-link-padding-x: 16px;
        --bs-nav-link-padding-y: 12px;
        --bs-nav-link-font-weight: var(--tc-fw-semibold);
        --bs-nav-link-color: var(--tc-system-navy-blue-3);
        --bs-nav-link-hover-color: var(--tc-theme-primary);
    }
    .nav-link {
        border-radius: 8px;
        background: #f3f3f3;
        display: block;
        padding: var(--bs-nav-link-padding-y) var(--bs-nav-link-padding-x);
        font-size: 14px;
        font-weight: var(--bs-nav-link-font-weight);
        color: var(--bs-nav-link-color);
    }
    .nav-tabs .nav-item.show .nav-link,
    .nav-tabs .nav-link.active {
        color: var(--bs-nav-tabs-link-active-color);
        background-color: var(--tc-theme-primary);
        border-color: transparent;
    }
}
.card-contact {
    padding: 32px;
    transition: all 0.2s linear;
    border-radius: 16px;
    border: 1px solid rgba(17, 17, 17, 0.1);
    background: var(--tc-bg-light);
    .text-opacity-90 {
        opacity: 0.9;
    }
    &:hover {
        border-color: var(--tc-theme-primary);
    }
}
.digital-agency-contact-section-1 {
    .form-group {
        border-radius: 16px;
        border: 1px solid rgba(17, 17, 17, 0.1);
        background: var(--tc-bg-light);
        .form-control {
            color: var(--tc-system-black);
            border-radius: 16px;
            border: 1px solid rgba(17, 17, 17, 0.1);
            background: rgba(255, 255, 255, 0.05);
        }
        textarea {
            min-height: 170px;
        }
        .form-select {
            background-color: rgba(255, 255, 255, 0.05);
            border: 1px solid rgba(17, 17, 17, 0.1);
            border-radius: 16px;
            &:focus {
                outline: none;
                box-shadow: none;
            }
        }
    }
}
.digital-agency-case-study-section-1 {
    .card-case-study {
        min-height: 420px;
    }
}
.digital-agency-home-section-7 {
    .img-custom-anim-right {
        min-height: 300px;
    }
}
